.addCommentContainer {
    display: grid;
    grid-template-columns: 10% 90%;
    box-shadow: 0 -15px 0 0 #d4feff, 0 0 5px 4px rgba(0, 0, 0, 0.09);
    border: 1px solid transparent;
    border-top: 1px solid #ace9eb;
    background: #d4feff;
    
}

.addCommentIcon {
    grid-row: 1/1;
    grid-column: 1/1;
    background-color: #5995ED;
    border-radius: 50vw;
    padding: 0.5vw 0.5vw;
    justify-self: center;
    font-size: 10px;
    color: white;
    margin-top: 12px;
    height: max-content;
}

.addCommentForm {
    display: grid;
    grid-template-columns: 80% 20%;
    padding: 10px 0;
    padding-right: 10px;
}

.addCommentInput {
    font-size: 11px;
    border: transparent;
    outline: none;
    padding: 0 5px;
    margin-top: 5px;
    color: #463239;
}

.addCommentInput:empty::before {
    content: "Type your comment...";
    color: gray;
}

.addCommentButton {
    background-color: #5995ED;
    border: none;
    border-radius: 10px;
    color: white;
    font-size: 12px;
    width: max-content;
    height: max-content;
    padding: 5px 15px;
    justify-self: end;
    align-self: end;
    cursor: pointer;
    transition: background-color 0.2s;
    font-weight: 600;
}

.addCommentButton:hover {
    background-color: #4778c2;
}

@media screen and (max-width: 340px) {
    .addCommentContainer {
        grid-template-columns: 15% 85%;
    }
    .addCommentIcon {
       padding: 5px 5px;
       font-size: 12px;
   }
   .addCommentInput {
       grid-column: 1/3;
        font-size: 13px;
        grid-row: 1/1;
        width: 100%;
   }
   .addCommentButton {
       font-size: 14px;
       grid-row: 2/2;
       grid-column: 2/2;
   }
}

